@import "../styles/variable.scss";

.nav-header {
  position: relative;
  width: 100%;
  color: $font-color-header;

  .container {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    min-width: 800px;
    max-width: 1080px;
  }

  .button {
    display: inline-block;
    padding: 0 10px 2px;
    border: 1px solid $font-color-default;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;

    .label {
      margin-left: 8px;
      font-size: 12px;
      vertical-align: middle;
    }
  }

  .logo-wrapper {
    display: flex;
    flex-direction: column;
    height: 72px;

    .logo {
      padding: 5px 0;

      img {
        vertical-align: middle;
      }

      .whitepaper {
        margin-left: 20px;
        vertical-align: middle;
      }
    }

    .logo-mobile {
      display: none;
    }

    img {
      display: inline-block;
      width: 180px;
      height: auto;
    }

    p {
      font-size: 12px;
      line-height: 1;
    }

    .white-paper {
      margin-left: 20px;
    }
  }

  .navtab {
    display: inline-block;
    width: 100px;
    text-align: center;
    color: rgba(0, 0, 0, 0.54);
    cursor: pointer;
    font-size: 1.2rem;

    &.active {
      color: #C05D4F;
      border-bottom:  #C05D4F solid 1px;
    }
    &.hover {
      color: #C05D4F;
    }
  }

  .wallet-mobile {
    display: none;
  }
}

.menu-dropdown {
  .ant-dropdown-menu-item {
    font-size: 18px;
    color: $background-color;
  }
}

@media screen and (max-width: 720px) {
  .nav-header {
    .container {
      max-width: 720px;
      min-width: 320px;
    }
    .logo-wrapper {
      .logo-pc {
        display: none;
      }
      .logo-mobile {
        display: block;
        padding: 15px 5px;
      }
    }
    .navtab {
      width: 60px;
    }
    .wallet-pc {
      display: none;
    }
    .wallet-mobile {
      display: block;
      cursor: pointer;
      padding: 5px;
    }
  }
}
